
ul {
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: rgb(160, 231, 161);
    display: flex;
  justify-content: center;
  align-items: center;

  }
  
  li {
    float: left;
    display: block;
    color: rgb(197, 65, 65);
    text-align: center;
    padding: 16px;
  }
  li a {
    text-decoration: none;
  }
  
  li:hover {
    background-color: rgb(93, 208, 199);
  }