div{
    margin: 0%;
    padding: 0%;
    background-color: rgb(160, 231, 161);
}


body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
  }
  
  .container {
    max-width: 900px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .header {
    text-align: center;
  }

  .header h2{
    color: rgb(215, 54, 54);
  }
  
  .main-content {
    margin: 20px 0;
  }
  
  .profile {
    text-align: center;
    margin-bottom: 20px;
    color: rgb(194, 31, 31);
  }

  .profile .n1, .n2{
    color: rgb(194, 31, 31);
    font-weight: bold
  }
  
  .profile-photo {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin-bottom: 10px;
  }
  
  h2 {
    color: #333;
    margin-bottom: 10px;
  }
  
  .about,
  .contact,
  .skills,
  .experience {
    margin-bottom: 30px;
  }
  
  .skills ul {
    list-style-type: none;
    padding: 0;
  }
  
  .skills li {
    margin: 5px 0;
    padding: 10px;
  }
  
  .footer {
    text-align: center;
    padding: 10px;
    background-color: #4CAF50;
    color: white;
  }
  