.cal {
    width: 300px;
    margin: auto;
    border: 3px solid black;
    margin-top: 20px;
    padding: 10px;
    background-color: yellowgreen;
    color: azure;
    border-radius: 10px;

}
.cal button {

    padding: 25px;
    margin: 1px;
    background-color: whitesmoke;
    width: 70px;
    cursor: pointer;
    border-radius: 12px;
    font-weight: bold;
    font-size: medium;
    -webkit-box-shadow: 5px 10px 5px 0px rgb(30, 28, 29);
    -moz-box-shadow: 5px 10px 5px 0px  rgb(30, 28, 29);
    box-shadow: 5px 10px 5px 0px rgb(30, 28, 29);

}

.cal button .ac,.equal{
    width: 140px;

}

.cal input {
    background-color: white;
    font-weight: bold;
    border-radius: 10px;
    justify-content: center;
    align-items: center;
    display: flex;
    margin: auto;
    width: 250px;
    height: 30px;
}

.cal p {
    margin: auto;
    width: 100px;
    color: rgb(61, 54, 134);
    font-size: x-large;
    font-weight: bolder;
}

.ac, .equal {
    width: 140px;
}

button:active {
    animation: pusheffect 2s infinite;
    -webkit-animation: fade-out-top 2.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
    animation: fade-out-top 2.5s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;

}

@keyframes pusheffect {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(0.7);
    }

    100% {
        transform: scale(1);
    }
}


@-webkit-keyframes fade-out-top {
    0% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
        opacity: 1;
    }

    100% {
        -webkit-transform: translateY(-50px);
        transform: translateY(-50px);
        opacity: 0;
    }
}

@keyframes fade-out-top {
    0% {
        -webkit-transform: translateY(0);
        transform: translateY(0);
        opacity: 1;
    }

    100% {
        -webkit-transform: translateY(-50px);
        transform: translateY(-50px);
        opacity: 0;
    }
}

button:hover {
    background-color: rgb(34, 245, 136);
  }